.App {
  color: white;
  margin-top: 7%;
  font-family: "open sans";
}
.content{
  margin: 2.3% 6.7% 0% 6.7%;
}

.head{
  width: 50%;
  font-size: 18px;
  margin: 6.7%;
}
.footer{
padding-top: 70vh;
}

#goGreen{
  color: #009933;
  font-size: 24px;
}
.about{
  margin:35% 6.7% 6.7% 6.7%;
  font-size: 2.5vw;
  margin-top: 65vh;
}
.img{
  animation: float 5s infinite;
  float: left;
  animation-direction: alternate-reverse;
}
@keyframes float{
  0%{
    transform: translate(0%, 0%);
  }
  100%{
    transform: translate(-7%, 7%);
  }
}
@media only screen and (min-width: 1000px) {
    .about {
        font-size: 23px;
    }
}
@media only screen and (max-width: 600px) {
   .about{
     font-size: 18px;
   }
}